@import "../../global.scss";

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  background: url("../../../public/img/contact.jpg") no-repeat center
    center/cover;

  .sidecontact {
    width: 14%;
    height: 45%;
    background-color: #0f3959;
    z-index: 20;
    margin-right: 45%;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .info {
      margin-top: 30px;
      color: rgba(160, 154, 154, 0.596);

      h2 {
        margin-left: 20px;
        color: white;
      }

      .mailcontainer {
        margin-top: 20px;

        .mail {
          margin-left: 20px;
        }
        .m {
          margin-left: 20px;
        }
      }

      .phonecontainer {
        margin-top: 20px;
        .phone {
          margin-left: 20px;
        }
        .p {
          margin-left: 20px;
        }
      }
    }

    .socialmedia {
      ul {
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin-right: 40px;
        li {
          a {
            color: rgba(94, 89, 89, 0.637);

            &:hover {
              color: white;
            }
          }
        }
      }
    }
  }

  .contact {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: absolute;

    button {
      width: 20%;
      height: 30px;
      color: white;
      background-color: #0f3959;
      border: none;
      border-radius: 10px;
      font-weight: 500;
      cursor: pointer;
      margin-top: 40px;
      margin-right: 42%;
      &:hover {
        opacity: 0.9;
      }
    }

    textarea {
      width: 71%;
      height: 100px;
      font-size: 14px;
      border: none;
      border-bottom: lightgray solid 1px;
      margin-left: 10%;
    }

    form {
      width: 45%;
      height: 50%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-color: white;
      border-radius: 10px;

      .changedirection {
        width: 90%;
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin-left: 10%;
      }
    }

    h2 {
      margin-right: 51%;
      margin-bottom: 10px;
      color: #0f3959;
    }

    input {
      width: 300px;
      height: 30px;
      font: 14px;
      margin-bottom: 20px;
      border: none;
      border-bottom: lightgray solid 1px;
    }
  }
}
